import React from 'react'

import Layout from "../components/layout"
import SEO from "../components/seo"

import styles from './index.module.css'
import Rs from "../components/rs"
import { Link } from 'gatsby'

export default() => (
    <Layout>
        <SEO title='リソースパック'/>
        <h1>リソースパック</h1>
        <p>ぽっけドールではサーバーリソースパックを配布しています。<br/>サーバーにログインすると自動的に適用されますが、通信状況などからリソースパックをオフにしないとログインできない場合などはこちらからダウンロードすることも可能です。</p>

        <h2>現在公開されているリソースパック</h2>
        <p>サーバーリソースパック: <Link to="">316L</Link></p>
        <Rs version="317LD" link="https://www.dropbox.com/s/5oyj6x1qp7pfbeu/doll.317LD.zip?dl=1" info="矢が刺さる音を削除"/>
        <Rs version="316L" link="https://www.dropbox.com/s/9ew4boa4ebazsvh/doll.316L.zip?dl=1" info="テクスチャの追加"/>
        <Rs version="315LF" link="https://www.dropbox.com/s/cz7g4159qe1b1dl/doll.315LF.zip?dl=1" info="315Lの修正"/>
        <Rs version="315L" link="https://www.dropbox.com/s/8vdn90tm4va5xx6/doll.315L.zip?dl=0" info="銃テクスチャの追加"/>
        <hr/>
        <h2>命名規則</h2>
        <p>
            リソースパックのバージョンには一定の規則があります。<br/>
            バージョンは'パックフォーマット' + '更新回数' + '構成種類' + '備考'の形で命名されます。
        </p>
        <h4>パックフォーマット</h4>
        <p>1.11, 1.12では3, 1.13からは4です。Minecraftのバージョンがパックフォーマットと合わない場合、リソースパックの読み込みにエラーが出る場合があります。</p>
        <h4>構成種類</h4>
        <p>"H"は3Dモデルを中心に構成、"L"は2Dモデルに中心に構成されています。</p>
        <h4>備考</h4>
        <ul>
            <li>D: 開発版リソースパック。テクスチャにエラーまたはリソースパック自体にエラーが発生する場合がある</li>
            <li>F: 更新回数に含まないレベルの修正をしたリソースパック</li>
            <li>M: L型リソースパックから3Dを取り払ったバージョンのリソースパック。ファイルサイズは小さくなる</li>
            <li>O: テクスチャを最適化したリソースパック。ファイルサイズは小さくなる</li>
            <li>MO: MとOを足したもの</li>
        </ul>
    </Layout>
)