import React from 'react'
import PropTypes from "prop-types"

import {Link} from "gatsby"

const Rs = ({version, link, info}) => (
    <div style={{
        margin: 0,
        padding: "5px 10px",
        backgroundColor: "#f5f5f5"
    }}>
        <h3>{version}</h3>
        {info !== ""} <p>{info}</p>
        <a href={link} target="_blank">ダウンロード</a>
    </div>
)

Rs.propTypes = {
    version: PropTypes.string,
    link: PropTypes.string,
    info: PropTypes.string
}

Rs.defaultProps = {
    version: "",
    link: "404",
    info: ""
}

export default Rs